import { HTTPClient } from 'koajax';

// @credit: https://lab.ahusmart.com/nCoV/api/ 
// @credit: https://lab.isaaclin.cn/nCoV/ 

export const epidemic = new HTTPClient({
    baseURI: 'https://lab.ahusmart.com/nCoV/api/',
    responseType: 'json'
});

interface MapData<T = {}> {
    results: T[];
}

export interface City {
    cityName: string;
}

export interface Province {
    provinceShortName: string;
    cities?: City[];
    updateTime: number;
}

export async function getOverall() {
    const {
        body: { results }
    } = await epidemic.get<MapData>('overall?latest=0');

    return results;
}

export async function getHistory() {
    const {
        body: { results }
    } = await epidemic.get<MapData<Province>>('area?latest=0');

    return results;
}

export async function getCurrent() {
    const {
        body: { results }
    } = await epidemic.get<MapData<Province>>('area');

    return results;
}
